    .check.active .checkbar{
            @apply opacity-100
        }
        .check.active {
            @apply bg-[#FEC33C]
        }
        .askproject:hover + .asking{
            @apply opacity-100 max-h-[10000px];
            overflow: unset !important;
        }
        .period.active{
            @apply bg-[#FEC33C]
        }
        .cek:hover .ini{
            @apply opacity-100
        }
        .inputBox input:valid ~ label,
        .inputBox input:focus ~ label,
        .inputBox input:valid ~ label.req::after,
        .inputBox input:focus ~ label.req::after{
            @apply -translate-y-5 text-sm text-[#6D727E]
        }
        
        /* .inputBox textarea:valid ~ label,
        .inputBox textarea:focus ~ label{
            @apply -translate-y-5 text-sm
        } */
        /* textarea:focus ~ label{
            @apply -translate-y-5 text-sm
        } */
      .inputBox [required] ~ label.req::after{
        @apply content-['*'] text-red-600
      }
      input[type=file]{
        @apply cursor-pointer
      }
      .thisCheck input[type=checkbox]{
       color: #FEC33C; 
       border-radius:100px;
       background-image: none;
      }
      .thisCheck input[type=checkbox]:checked {
        background-image: none;
      }
      .thisCheck.active .checkbar{
        @apply opacity-100
      }
      input[type=radio]{
        @apply ring-0 border-[1px] border-[#EDEDEF];
        color: #FEC33C;
        border-radius: 12px;
        outline: none;
        transition-duration: 0.5s;
      }

      input[type=radio]:checked{
        @apply focus:ring-0 outline-none border-[1px] border-[#EDEDEF];
        background-image: none;
      }
      input[type=radio]:checked:focus{
        @apply border-[1px] border-[#EDEDEF];
        background-image: none;
      }
      input[type=radio]:focus{
        @apply ring-0 outline-none border-[1px] border-[#EDEDEF] 
      }
      .form-radio:checked:hover{
        @apply border-[1px] border-[#EDEDEF]
      }
      #labelInput.active{
        @apply  -translate-y-5 text-sm text-[#6D727E]
      }
      .file-item{
        @apply flex gap-[11px] px-[19px] items-center w-[210px] h-[40px] rounded-full border-2 border-[#EDEDEF] relative overflow-hidden
      }
      .file-item-name{
        @apply overflow-hidden text-ellipsis whitespace-nowrap text-[14px] text-center flex items-center gap-1
      }
      .close-button {
          @apply absolute right-4 top-1/2 -translate-y-1/2
        }
        .thanks.active{
          @apply opacity-100 z-50
        }

   